import { ReactComponent as CarriegeIcon } from "../../assets/images/catalogList/carriage.svg";
import { ReactComponent as RoomIcon } from "../../assets/images/catalogList/room.svg";
import { ReactComponent as ChairIcon } from "../../assets/images/catalogList/chair.svg";
import { ReactComponent as FeedingIcon } from "../../assets/images/catalogList/feeding.svg";
import { ReactComponent as SoapIcon } from "../../assets/images/catalogList/soap.svg";
import { ReactComponent as AutochairIcon } from "../../assets/images/catalogList/autochair.svg";
import { ReactComponent as CarIcon } from "../../assets/images/catalogList/car.svg";
import { ReactComponent as ToyIcon } from "../../assets/images/catalogList/toy.svg";
import { ReactComponent as ClothesIcon } from "../../assets/images/catalogList/clothes.svg";
import { ReactComponent as NewIcon } from "../../assets/images/catalogList/new.svg";

import styles from "./catalogList.module.scss";

const CatalogList = () => {
  return (
    <ul className={styles.catalogList}>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <CarriegeIcon className={styles.icon} />
          <span>Дитячі коляски</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <RoomIcon className={styles.icon} />
          <span>Дитяча кімната</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <ChairIcon className={styles.icon} />
          <span>Стільці і шезлонги</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <FeedingIcon className={styles.icon} />
          <span>Для годування</span>
        </a>
      </li>

      <li className={styles.item}>
        <a className={styles.link} href="*">
          <SoapIcon className={styles.icon} />
          <span>Гігієна і догляд</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <AutochairIcon className={styles.icon} />
          <span>Автокрісла</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <CarIcon className={styles.icon} />
          <span>Дитячий транспорт</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <ToyIcon className={styles.icon} />
          <span>Іграшки</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <ClothesIcon className={styles.icon} />
          <span>Одяг</span>
        </a>
      </li>
      <li className={styles.item}>
        <a className={styles.link} href="*">
          <NewIcon className={styles.icon} />
          <span>Новий товар</span>
        </a>
      </li>
    </ul>
  );
};

export default CatalogList;
