import { NavLink } from "react-router-dom";
import { useState } from "react";
import { nanoid } from "nanoid";
import { disableBodyScroll, enableBodyScroll } from "body-scroll-lock";

import HeaderBtns from "../HeaderBtns/HeaderBtns";
import NavBar from "../NavBar/NavBar";
import ContactsBlock from "../ContactsBlock/ContactsBlock";
import MobileMenu from "../BurgerMenu/MobileMenu";
import CatalogList from "../CatalogList/CatalogList";

import { ReactComponent as DesktopMenuIcon } from "../../assets/images/icon/desktopMenu.svg";
import { ReactComponent as LogoIcon } from "../../assets/images/icon/logo.svg";
import { ReactComponent as BurgerMenu } from "../../assets/images/icon/menu.svg";
import { ReactComponent as SearchIcon } from "../../assets/images/icon/inputSearch.svg";
import { ReactComponent as CloseIcon } from "../../assets/images/icon/close.svg";

import styles from "./header.module.scss";

const Header = () => {
  const [openMenu, setOpenMenu] = useState(false);
  const [openCatalog, setOpenCatalog] = useState(false);
  const targetElement = document.querySelector("body");

  const handleOpenMenu = () => {
    setOpenMenu(true);
    disableBodyScroll(targetElement);
  };

  const handleCloseMenu = () => {
    if (openMenu) setOpenMenu(false);
    enableBodyScroll(targetElement);
  };

  const handleOpenCatalog = () => {
    setOpenCatalog(true);
  };

  const handleCloseCatalog = () => {
    setOpenCatalog(false);
  };

  const id = nanoid();

  return (
    <div className={styles.header}>
      <div className={styles.container}>
        <div className={styles.upContainer}>
          <div className={styles.leftContainer}>
            <button
              type="button"
              className={styles.burgerMenuBtn}
              onClick={handleOpenMenu}
            >
              {!openMenu && <BurgerMenu className={styles.burgerMenu} />}
            </button>
            <NavLink to={"/"}>
              <LogoIcon className={styles.logo} />
            </NavLink>
            <div className={styles.navbar}>
              <NavBar />
            </div>
          </div>
          <div className={styles.rightContainer}>
            <ContactsBlock />
          </div>
        </div>

        <div
          className={styles.catalogContainer}
          onMouseEnter={handleOpenCatalog}
          onMouseLeave={handleCloseCatalog}
        >
          <button className={styles.btnMenu} type="button">
            <DesktopMenuIcon />
          </button>
          <NavLink to={"/catalog"} className={styles.text}>
            Каталог
          </NavLink>

          {openCatalog && (
            <div className={styles.catalogListWrapper}>
              <CatalogList />
            </div>
          )}
        </div>

        <div className={styles.downContainer}>
          <div className={styles.inputContainer}>
            <input id={id} className={styles.input} placeholder="Я шукаю..." />
            <button type="button" className={styles.srchBtn}>
              <SearchIcon />
            </button>
          </div>

          {!openMenu && <HeaderBtns />}
        </div>
      </div>

      {openMenu && (
        <button className={styles.closeBtn} onClick={handleCloseMenu}>
          <CloseIcon />
        </button>
      )}
      {openMenu && <MobileMenu />}
    </div>
  );
};

export default Header;
