import { useState } from "react";

import { NavLink } from "react-router-dom";

import Section from "../Section/Section";
import NavBar from "../NavBar/NavBar";
import ContactsBlock from "../ContactsBlock/ContactsBlock";
import CatalogList from "../CatalogList/CatalogList";

import { ReactComponent as BurgerMenu } from "../../assets/images/icon/desktopMenu.svg";
import { ReactComponent as LeftArrowIcon } from "../../assets/images/icon/leftArrow.svg";

import styles from "./mobileMenu.module.scss";

const MobileMenu = () => {
  const [openCatalog, setOpenCatalog] = useState(false);

  const handleOpenCatalog = () => {
    setOpenCatalog(true);
  };

  const handleCloseCatalog = () => {
    setOpenCatalog(false);
  };
  return (
    <div className={styles.menuWrapper}>
      <Section>
        <div className={styles.catalogContainer}>
          <button
            className={styles.btn}
            type="button"
            onClick={handleOpenCatalog}
          >
            <BurgerMenu />
          </button>
          <NavLink to={"/catalog"} className={styles.text}>
            Каталог
          </NavLink>
        </div>
        <NavBar />
        <ContactsBlock />

        {openCatalog && (
          <div className={styles.catalogListWrapper}>
            <Section>
              <button
                className={styles.btnBack}
                type="button"
                onClick={handleCloseCatalog}
              >
                <LeftArrowIcon />
                <span className={styles.title}>Меню</span>
              </button>

              <CatalogList />
            </Section>
          </div>
        )}
      </Section>
    </div>
  );
};

export default MobileMenu;
